/* section services */
.service {
    font-family: "iA Writer Mono";
    height: 100%;
    text-align: center;

}
.service .service_title{
    font-family: "iA Writer Mono";
    font-size: 16px !important;
    font-weight: 500 !important;
    line-height: 35px !important;
    text-align: center !important;
}
